'use client';

import { useState, useEffect } from 'react';
import { useAuth } from '@/context/AuthContext';
import { useRouter } from 'next/navigation';
import { collection, doc, writeBatch, query, orderBy, limit, onSnapshot, getDocs, Timestamp, addDoc } from 'firebase/firestore';
import { db } from '@/lib/firebase';
import { AnimatePresence, motion } from 'framer-motion';
import {
    Zap, Users, Trophy, Shield, Activity,
    Search, X, Check, AlertCircle, History,
    Menu, LogOut, ChevronRight
} from 'lucide-react';

// --- Types ---
interface UserData {
    id: string;
    displayName: string;
    phone?: string;
    exp: number;
    rank: string;
    partyId?: string;
}

interface LogEntry {
    id: string;
    timestamp: any;
    adminName: string;
    action: string;
    details: string;
}

// --- Config (Same as App Script) ---
const ACTIVITY_LIST = [
    { id: 'gym_visit', label: '1. ‡∏°‡∏≤ Gym / ‡πÉ‡∏™‡πà‡πÄ‡∏™‡∏∑‡πâ‡∏≠ Rank', exp: 1 },
    { id: 'gym_win1', label: '2. ‡∏ä‡∏ô‡∏∞ 2 ‡∏£‡∏≠‡∏ö (‡∏¢‡πà‡∏≠‡∏¢)', exp: 1 },
    { id: 'gym_win2', label: '3. ‡∏ä‡∏ô‡∏∞ 4 ‡∏£‡∏≠‡∏ö (Match)', exp: 2 },
    { id: 'gym_top1', label: '4. ‡∏ó‡∏µ‡πà 1 Gym Tournament', exp: 3 },
    { id: 'quest_photo', label: '5. ‡πÄ‡∏Ñ‡∏ß‡∏™‡∏ñ‡πà‡∏≤‡∏¢‡∏£‡∏π‡∏õ', exp: 1 },
    { id: 'quest_review', label: '6. ‡πÄ‡∏Ñ‡∏ß‡∏™‡∏£‡∏µ‡∏ß‡∏¥‡∏ß‡∏Ñ‡∏π‡πà‡πÅ‡∏Ç‡πà‡∏á', exp: 2 },
    { id: 'shop_support', label: '7. ‡∏ã‡∏∑‡πâ‡∏≠‡∏Ç‡∏≠‡∏á‡∏£‡πâ‡∏≤‡∏ô (>1,500)', exp: 5 },
    { id: 'shop_preorder', label: '8. Pre-order Box', exp: 5 },
];

const RANK_THRESHOLDS = [
    { min: 200, name: 'Legend' }, { min: 120, name: 'Grandmaster' },
    { min: 75, name: 'Diamond' }, { min: 50, name: 'Platinum' },
    { min: 30, name: 'Gold' }, { min: 15, name: 'Silver' },
    { min: 5, name: 'Bronze' }, { min: 0, name: 'Rookie' }
];

export default function AdminControlPanel() {
    const { user, loading } = useAuth();
    const router = useRouter();

    // Data
    const [users, setUsers] = useState<UserData[]>([]);
    const [logs, setLogs] = useState<LogEntry[]>([]);

    // State for Flow
    const [stage, setStage] = useState<'IDLE' | 'ADMIN_NAME' | 'SELECT_ACT' | 'SELECT_USER' | 'CONFIRM' | 'SUCCESS'>('IDLE');
    const [menuType, setMenuType] = useState<'ACTIVITY' | 'MANUAL' | 'PARTY' | null>(null);

    // Form Data
    const [adminName, setAdminName] = useState('');
    const [selectedAct, setSelectedAct] = useState<any>(null);
    const [manualExp, setManualExp] = useState(0);
    const [manualReason, setManualReason] = useState('');
    const [targetUser, setTargetUser] = useState<UserData | null>(null);
    const [prevAdminName, setPrevAdminName] = useState('');

    // Search
    const [searchTerm, setSearchTerm] = useState('');
    const [isProcessing, setIsProcessing] = useState(false);

    // --- Init ---
    useEffect(() => {
        if (!loading) {
            if (!user) router.push('/');
            else {
                // Load cached admin name
                const savedName = localStorage.getItem('adminName');
                if (savedName) setPrevAdminName(savedName);

                // Fetch Users
                getDocs(query(collection(db, 'users'))).then(snap => {
                    setUsers(snap.docs.map(d => ({ id: d.id, ...d.data() } as UserData)));
                });

                // Subscribe Logs
                const unsub = onSnapshot(query(collection(db, 'admin_logs'), orderBy('timestamp', 'desc'), limit(10)), (snap) => {
                    setLogs(snap.docs.map(d => ({ id: d.id, ...d.data() } as LogEntry)));
                });
                return () => unsub();
            }
        }
    }, [user, loading, router]);

    // --- Logic ---
    const calculateRank = (exp: number) => RANK_THRESHOLDS.find(r => exp >= r.min)?.name || 'Rookie';

    const startFlow = (type: 'ACTIVITY' | 'MANUAL' | 'PARTY') => {
        setMenuType(type);
        setAdminName(prevAdminName); // Prefill
        setStage(prevAdminName ? 'SELECT_ACT' : 'ADMIN_NAME'); // Skip name if known
        if (type === 'MANUAL') setSelectedAct({ label: 'Manual Adjustment', exp: 0 });
    };

    const handleNextStep = () => {
        if (stage === 'ADMIN_NAME') {
            if (!adminName.trim()) return alert('‡∏Å‡∏£‡∏∏‡∏ì‡∏≤‡∏Å‡∏£‡∏≠‡∏Å‡∏ä‡∏∑‡πà‡∏≠‡πÅ‡∏≠‡∏î‡∏°‡∏¥‡∏ô');
            localStorage.setItem('adminName', adminName);
            setPrevAdminName(adminName);
            setStage(menuType === 'ACTIVITY' ? 'SELECT_ACT' : 'SELECT_USER');
        }
        else if (stage === 'SELECT_ACT') {
            if (!selectedAct) return alert('‡∏Å‡∏£‡∏∏‡∏ì‡∏≤‡πÄ‡∏•‡∏∑‡∏≠‡∏Å‡∏Å‡∏¥‡∏à‡∏Å‡∏£‡∏£‡∏°');
            setStage('SELECT_USER');
        }
        else if (stage === 'SELECT_USER') {
            if (!targetUser) return alert('‡∏Å‡∏£‡∏∏‡∏ì‡∏≤‡πÄ‡∏•‡∏∑‡∏≠‡∏Å‡∏™‡∏°‡∏≤‡∏ä‡∏¥‡∏Å');
            setStage('CONFIRM');
        }
    };

    const handleExecute = async () => {
        if (!targetUser) return;
        setIsProcessing(true);

        try {
            const batch = writeBatch(db);
            const timestamp = Timestamp.now();

            let expChange = 0;
            let details = '';

            if (menuType === 'ACTIVITY') {
                expChange = selectedAct.exp;
                details = `Activity: ${selectedAct.label}`;
            } else if (menuType === 'MANUAL') {
                expChange = manualExp;
                details = `Manual: ${manualReason}`;
            }

            // 1. Update User
            const newExp = Math.max(0, targetUser.exp + expChange);
            const newRank = calculateRank(newExp);
            const userRef = doc(db, 'users', targetUser.id);
            batch.update(userRef, { exp: newExp, rank: newRank });

            // 2. Add Log
            const logRef = doc(collection(db, 'admin_logs'));
            batch.set(logRef, {
                timestamp,
                adminName: adminName || 'Unknown',
                action: menuType === 'ACTIVITY' ? 'Add EXP' : 'Manual Edit',
                details: `${targetUser.displayName} (${expChange > 0 ? '+' : ''}${expChange} EXP) - ${details}`,
                targetId: targetUser.id
            });

            await batch.commit();

            setStage('SUCCESS');
            // Reset after 1.5s
            setTimeout(() => {
                setStage('IDLE');
                setTargetUser(null);
                setSelectedAct(null);
                setManualExp(0);
                setManualReason('');
                setSearchTerm('');
            }, 1500);

        } catch (e) {
            alert('Error: ' + e);
        } finally {
            setIsProcessing(false);
        }
    };

    // --- Render ---
    if (loading) return <div className="h-screen bg-slate-900 grid place-items-center text-white">Loading...</div>;

    return (
        <div className="min-h-screen bg-slate-900 text-slate-100 font-sans pb-20">

            {/* Navbar */}
            <div className="bg-slate-800 p-4 shadow-md flex justify-between items-center sticky top-0 z-10">
                <h1 className="font-bold text-lg flex items-center gap-2">
                    <Shield className="text-blue-500" /> Admin Panel
                </h1>
                <button onClick={() => router.push('/dashboard')} className="text-sm text-slate-400 font-bold hover:text-white">EXIT</button>
            </div>

            {/* Main Grid Menu */}
            <div className="max-w-md mx-auto p-6 space-y-6">

                {/* Admin Name Display */}
                {prevAdminName && (
                    <div className="flex justify-between items-center bg-slate-800/50 p-3 rounded-lg border border-slate-700">
                        <div className="text-xs text-slate-400">Current Admin:</div>
                        <div className="font-bold text-blue-400 flex items-center gap-2">
                            {prevAdminName}
                            <button onClick={() => { setPrevAdminName(''); setAdminName(''); localStorage.removeItem('adminName'); }} className="p-1 hover:bg-slate-700 rounded"><X className="w-3 h-3 text-slate-500" /></button>
                        </div>
                    </div>
                )}

                <div className="grid grid-cols-1 gap-4">
                    <button onClick={() => startFlow('ACTIVITY')} className="h-24 bg-gradient-to-br from-blue-600 to-blue-800 rounded-2xl flex items-center justify-center gap-3 shadow-lg active:scale-95 transition-transform">
                        <Zap className="w-8 h-8 text-white" />
                        <div className="text-left text-white">
                            <div className="font-bold text-lg">‡πÄ‡∏û‡∏¥‡πà‡∏° EXP ‡∏Å‡∏¥‡∏à‡∏Å‡∏£‡∏£‡∏°</div>
                            <div className="text-xs opacity-70">Add Activity EXP</div>
                        </div>
                    </button>

                    <button onClick={() => startFlow('MANUAL')} className="h-20 bg-slate-800 border border-slate-700 rounded-xl flex items-center justify-center gap-3 hover:bg-slate-750 active:scale-95 transition-transform p-4">
                        <Activity className="w-6 h-6 text-green-400" />
                        <div className="text-left">
                            <div className="font-bold">‡πÄ‡∏û‡∏¥‡πà‡∏° EXP Manual</div>
                            <div className="text-xs text-slate-500">‡∏Å‡∏£‡∏ì‡∏µ‡∏û‡∏¥‡πÄ‡∏®‡∏© / ‡πÅ‡∏Å‡πâ‡πÑ‡∏Ç</div>
                        </div>
                    </button>

                    <button disabled className="h-20 bg-slate-800/50 border border-slate-700 rounded-xl flex items-center justify-center gap-3 opacity-50 cursor-not-allowed">
                        <Users className="w-6 h-6 text-purple-400" />
                        <div className="text-left">
                            <div className="font-bold">‡∏à‡∏±‡∏î‡∏Å‡∏≤‡∏£ Party (Coming Soon)</div>
                            <div className="text-xs text-slate-500">‡∏™‡∏£‡πâ‡∏≤‡∏á / ‡∏¢‡πâ‡∏≤‡∏¢ / ‡∏•‡∏ö</div>
                        </div>
                    </button>
                </div>

                {/* Recent Logs Section */}
                <div className="mt-8">
                    <h3 className="text-sm font-bold text-slate-500 uppercase mb-3 flex items-center gap-2">
                        <History className="w-4 h-4" /> Recent Logs
                    </h3>
                    <div className="space-y-2">
                        {logs.map(log => (
                            <div key={log.id} className="text-xs bg-slate-800 p-3 rounded border-l-2 border-blue-500">
                                <div className="flex justify-between text-slate-500 mb-1">
                                    <span>{log.adminName}</span>
                                    <span>{log.timestamp?.toDate().toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}</span>
                                </div>
                                <div className="text-slate-300">{log.details}</div>
                            </div>
                        ))}
                        {logs.length === 0 && <div className="text-center text-slate-600 text-xs py-4">No logs available</div>}
                    </div>
                </div>
            </div>

            {/* --- MODAL WIZARD --- */}
            <AnimatePresence>
                {stage !== 'IDLE' && stage !== 'SUCCESS' && (
                    <div className="fixed inset-0 bg-black/90 z-50 flex items-end md:items-center justify-center p-4 backdrop-blur-sm">
                        <motion.div
                            initial={{ y: 100, opacity: 0 }}
                            animate={{ y: 0, opacity: 1 }}
                            exit={{ y: 100, opacity: 0 }}
                            className="bg-slate-800 w-full max-w-sm rounded-2xl border border-slate-700 shadow-2xl overflow-hidden mb-10 md:mb-0"
                        >
                            <div className="p-4 border-b border-slate-700 flex justify-between items-center bg-slate-900/50">
                                <h3 className="font-bold text-white flex items-center gap-2">
                                    {stage === 'ADMIN_NAME' && 'üë§ Step 1: Admin Name'}
                                    {stage === 'SELECT_ACT' && '‚ö° Step 2: Select Activity'}
                                    {stage === 'SELECT_USER' && 'üîç Step 3: Select User'}
                                    {stage === 'CONFIRM' && '‚ö†Ô∏è Confirm Action'}
                                </h3>
                                <button onClick={() => setStage('IDLE')}><X className="w-5 h-5 text-slate-400" /></button>
                            </div>

                            <div className="p-6 max-h-[60vh] overflow-y-auto">

                                {/* STEP 1: ADMIN NAME */}
                                {stage === 'ADMIN_NAME' && (
                                    <div>
                                        <label className="block text-sm font-bold text-slate-400 mb-2">‡∏Å‡∏£‡∏≠‡∏Å‡∏ä‡∏∑‡πà‡∏≠‡πÅ‡∏≠‡∏î‡∏°‡∏¥‡∏ô (Admin Name)</label>
                                        <input
                                            autoFocus
                                            type="text"
                                            value={adminName}
                                            onChange={e => setAdminName(e.target.value)}
                                            onKeyDown={e => e.key === 'Enter' && handleNextStep()}
                                            className="w-full bg-slate-900 border border-slate-600 rounded-lg p-3 text-white focus:border-blue-500 outline-none"
                                            placeholder="‡πÄ‡∏ä‡πà‡∏ô Nut, Champ"
                                        />
                                    </div>
                                )}

                                {/* STEP 2: SELECT ACTIVITY */}
                                {stage === 'SELECT_ACT' && (
                                    <div className="space-y-2">
                                        {menuType === 'ACTIVITY' ? (
                                            ACTIVITY_LIST.map(act => (
                                                <button
                                                    key={act.id}
                                                    onClick={() => { setSelectedAct(act); setTimeout(() => setStage('SELECT_USER'), 100); }}
                                                    className="w-full text-left p-3 rounded-lg bg-slate-700 hover:bg-blue-600 hover:text-white transition-colors flex justify-between items-center group"
                                                >
                                                    <span className="text-sm font-bold">{act.label}</span>
                                                    <span className="text-xs bg-slate-900/50 px-2 py-1 rounded group-hover:bg-white/20">+{act.exp}</span>
                                                </button>
                                            ))
                                        ) : (
                                            // Manual Mode Inputs
                                            <div className="space-y-3">
                                                <div>
                                                    <label className="text-xs font-bold text-slate-400">‡∏à‡∏≥‡∏ô‡∏ß‡∏ô EXP (+/-)</label>
                                                    <input
                                                        type="number" autoFocus
                                                        value={manualExp} onChange={e => setManualExp(Number(e.target.value))}
                                                        className="w-full bg-slate-900 border border-slate-600 rounded p-3 text-white text-lg font-mono font-bold"
                                                    />
                                                </div>
                                                <div>
                                                    <label className="text-xs font-bold text-slate-400">‡πÄ‡∏´‡∏ï‡∏∏‡∏ú‡∏• (Reason)</label>
                                                    <input
                                                        type="text"
                                                        value={manualReason} onChange={e => setManualReason(e.target.value)}
                                                        className="w-full bg-slate-900 border border-slate-600 rounded p-3 text-white"
                                                        placeholder="‡πÄ‡∏ä‡πà‡∏ô ‡∏ä‡∏ô‡∏∞‡∏Å‡∏¥‡∏à‡∏Å‡∏£‡∏£‡∏°‡∏û‡∏¥‡πÄ‡∏®‡∏©"
                                                    />
                                                </div>
                                            </div>
                                        )}
                                    </div>
                                )}

                                {/* STEP 3: SEARCH USER */}
                                {stage === 'SELECT_USER' && (
                                    <div>
                                        <div className="relative mb-3">
                                            <Search className="absolute left-3 top-3 text-slate-500 w-4 h-4" />
                                            <input
                                                autoFocus
                                                type="text"
                                                value={searchTerm}
                                                onChange={e => setSearchTerm(e.target.value)}
                                                className="w-full bg-slate-900 border border-slate-600 rounded-lg pl-10 p-3 text-white focus:border-blue-500 outline-none"
                                                placeholder="‡∏Ñ‡πâ‡∏ô‡∏´‡∏≤‡∏ä‡∏∑‡πà‡∏≠ ‡∏´‡∏£‡∏∑‡∏≠ ‡πÄ‡∏ö‡∏≠‡∏£‡πå‡πÇ‡∏ó‡∏£..."
                                            />
                                        </div>
                                        <div className="space-y-1 h-48 overflow-y-auto">
                                            {users.filter(u => u.displayName.toLowerCase().includes(searchTerm.toLowerCase())).map(u => (
                                                <button
                                                    key={u.id}
                                                    onClick={() => { setTargetUser(u); setTimeout(() => setStage('CONFIRM'), 50); }}
                                                    className="w-full text-left p-2 rounded hover:bg-blue-600 hover:text-white flex justify-between items-center group"
                                                >
                                                    <div>
                                                        <div className="text-sm font-bold">{u.displayName}</div>
                                                        <div className="text-[10px] text-slate-500 group-hover:text-blue-200">{u.phone || u.id}</div>
                                                    </div>
                                                    <div className="text-xs font-mono opacity-50">{u.exp} EXP</div>
                                                </button>
                                            ))}
                                        </div>
                                    </div>
                                )}

                                {/* STEP 4: CONFIRM */}
                                {stage === 'CONFIRM' && targetUser && (
                                    <div className="text-center space-y-4">
                                        <div className="w-16 h-16 bg-slate-700 rounded-full flex items-center justify-center mx-auto text-2xl font-bold text-white border-2 border-slate-600">
                                            {targetUser.displayName[0]}
                                        </div>
                                        <div>
                                            <h2 className="text-xl font-bold text-white">{targetUser.displayName}</h2>
                                            <div className="text-slate-400 text-sm">‡∏õ‡∏±‡∏à‡∏à‡∏∏‡∏ö‡∏±‡∏ô: {targetUser.exp} EXP ({targetUser.rank})</div>
                                        </div>

                                        <div className="bg-slate-900 p-4 rounded-xl border border-slate-700">
                                            <div className="text-xs text-slate-500 uppercase font-bold mb-1">Action Summary</div>
                                            <div className="flex items-center justify-center gap-2 text-blue-400 font-bold text-lg">
                                                {menuType === 'ACTIVITY' ? selectedAct.label : manualReason}
                                            </div>
                                            <div className={`text-2xl font-black mt-1 ${(menuType === 'ACTIVITY' ? selectedAct.exp : manualExp) > 0 ? 'text-green-400' : 'text-red-400'}`}>
                                                {(menuType === 'ACTIVITY' ? selectedAct.exp : manualExp) > 0 && '+'}{(menuType === 'ACTIVITY' ? selectedAct.exp : manualExp)} EXP
                                            </div>
                                        </div>

                                        <div className="text-xs text-slate-500">‡πÇ‡∏î‡∏¢ Admin: {adminName}</div>
                                    </div>
                                )}
                            </div>

                            {/* FOOTER BUTTONS */}
                            <div className="p-4 border-t border-slate-700 bg-slate-900/50 flex gap-3">
                                {stage === 'CONFIRM' ? (
                                    <>
                                        <button onClick={() => setStage('SELECT_USER')} className="flex-1 py-3 text-slate-400 font-bold text-sm bg-slate-800 rounded-lg">Back</button>
                                        <button onClick={handleExecute} disabled={isProcessing} className="flex-1 py-3 bg-blue-600 text-white font-bold rounded-lg shadow-lg shadow-blue-500/20 active:scale-95 transition-all">
                                            {isProcessing ? 'Processing...' : 'CONFIRM (‡∏¢‡∏∑‡∏ô‡∏¢‡∏±‡∏ô)'}
                                        </button>
                                    </>
                                ) : (
                                    <>
                                        {stage !== 'ADMIN_NAME' && <button onClick={() => setStage(prev => {
                                            if (prev === 'SELECT_ACT') return prevAdminName ? 'IDLE' : 'ADMIN_NAME';
                                            if (prev === 'SELECT_USER') return 'SELECT_ACT';
                                            return 'IDLE';
                                        })} className="text-slate-500 text-xs px-4">Back</button>}
                                        <button onClick={handleNextStep} className="flex-1 py-3 bg-blue-600 text-white font-bold rounded-lg active:scale-95 transition-all">
                                            NEXT &raquo;
                                        </button>
                                    </>
                                )}
                            </div>
                        </motion.div>
                    </div>
                )}

                {/* SUCCESS CONFIRMATION MODAL */}
                {stage === 'SUCCESS' && (
                    <div className="fixed inset-0 z-[60] flex items-center justify-center pointer-events-none">
                        <motion.div
                            initial={{ scale: 0.5, opacity: 0 }}
                            animate={{ scale: 1, opacity: 1 }}
                            exit={{ scale: 1.5, opacity: 0 }}
                            className="bg-green-500 p-6 rounded-3xl shadow-2xl flex flex-col items-center gap-2"
                        >
                            <div className="bg-white p-3 rounded-full text-green-500">
                                <Check className="w-8 h-8" strokeWidth={4} />
                            </div>
                            <div className="text-white font-black text-xl">SUCCESS!</div>
                        </motion.div>
                    </div>
                )}
            </AnimatePresence>

        </div>
    );
}
